import {
  OBJECT_NAV_REQUEST,
  OBJECT_NAV_FAIL,
  OBJECT_NAV_SUCCESS
} from '../constants/Nav';

const initialState = {
  objListData: null
}

export default function object(state = initialState, action) {
  switch (action.type) {
    case OBJECT_NAV_REQUEST:
      return state;
    case OBJECT_NAV_SUCCESS:
      return {...state, navData: action.payload.data};//
    case OBJECT_NAV_FAIL:
      return state;
    default:
      return state;
  }
}




