import * as React from "react";
import  {FormControl} from "react-bootstrap";

export interface NumericProps {
  handleChange: any ,
  value: string
}

export default class Numeric extends React.Component<NumericProps, undefined>  {

  constructor(props:NumericProps) {
    super(props);
  }

  render() {
    return <FormControl onChange={this.props.handleChange} value={this.props.value}/>
  }
}



