import React from 'react';
import {Link} from 'react-router';
import {connect} from 'react-redux';
import * as navAction from './../../actions/NavAction';
import {bindActionCreators} from 'redux';
import pubsub from 'pubsub-js';
import {Collapse} from 'react-bootstrap';
import SidebarRun from './Sidebar.run';

class Sidebar extends React.Component {

  constructor(props, context) {
    super(props, context);

    this.state = {
      userBlockCollapse: false,
      collapse: {
        singleview: this.routeActive(['singleview']),
        submenu: this.routeActive(['submenu'])
      }
    };
    this.pubsub_token = pubsub.subscribe('toggleUserblock', () => {
      this.setState({
        userBlockCollapse: !this.state.userBlockCollapse
      });
    });
  }

  componentDidMount() {
    SidebarRun();
  }

  componentWillMount() {
    this.props.navAction.loadNav({}, this.props.token);
  }


  componentWillUnmount() {
    // React removed me from the DOM, I have to unsubscribe from the pubsub using my token
    pubsub.unsubscribe(this.pubsub_token);
  }

  routeActive(paths) {
    paths = Array.isArray(paths) ? paths : [paths];
    for (let p in paths) {
      if (this.context.router.isActive(paths[p]) === true)
        return true;
    }
    return false;
  }

  toggleItemCollapse(stateName) {
    // var newCollapseState = {};
    for (let c in this.state.collapse) {
      if (this.state.collapse[c] === true && c !== stateName)
        this.state.collapse[c] = false;
    }
    this.setState({
      collapse: {
        [stateName]: !this.state.collapse[stateName]
      }
    });
  }

  render() {


    return (
      <aside className='aside'>
        { /* START Sidebar (left) */ }
        <div className='aside-inner'>
          <nav data-sidebar-anyclick-close='' className='sidebar'>
            { /* START sidebar nav */ }
            <ul className='nav'>
              { /* START user info */ }
              <li className='has-user-block'>
                <Collapse id='user-block' in={ this.state.userBlockCollapse }>
                  <div className='item user-block'>
                    { /* User picture */ }
                    <div className='user-block-picture'>
                      <div className='user-block-status'>
                        <img src='img/user/shek.jpg' alt='Avatar' width='60' height='60'
                             className='img-thumbnail img-circle'/>
                        <div className='circle circle-success circle-lg'></div>
                      </div>
                    </div>
                    { /* Name and Job */ }
                    <div className='user-block-info'>
                      <span className='user-block-name'>Hello, Shek</span>
                      <span className='user-block-role'>Developer</span>
                    </div>
                  </div>
                </Collapse>
              </li>
              { /* END user info */ }
              { /* Iterates over all sidebar items */ }
              <li className='nav-heading '>
                <span data-localize='sidebar.heading.HEADER'>Навигация</span>
              </li>
              {this.props.navData != null ? this.props.navData.map((obj, index)=> {
                  if (obj.folders != null) {
                    return <li className={ this.routeActive(['submenu']) ? 'active' : '' } key={index}>
                      <div className='nav-item' onClick={ this.toggleItemCollapse.bind(this, 'submenu_' + index) }>
                        <div className='pull-right label label-info'>{obj.cnt}</div>
                        <em className='icon-speedometer'></em>
                        <span data-localize='sidebar.nav.MENU'>{obj.name}</span>
                      </div>
                      <Collapse in={ this.state.collapse['submenu_' + index] } timeout={ 100 }>
                        <ul id='submenu' className='nav sidebar-subnav'>
                          <li className='sidebar-subnav-header'>{obj.name}</li>
                          {obj.folders.map((folder, index2)=>
                            <li
                              className={ this.routeActive(folder.type+(folder.id!=null ?('/'+folder.id):'')) ? 'active' : '' }
                              key={index2}>
                              <Link to={'/'+folder.type+(folder.id!=null ?('/'+folder.id):'')} title={folder.name}>
                                <span data-localize='sidebar.nav.SUBMENU'>{folder.name}</span>
                              </Link>
                            </li>
                          )}
                        </ul>
                      </Collapse>
                    </li>
                  } else {
                    return <li key={index} className={ this.routeActive(obj.type + (obj.id != null ? ('/' + obj.id) : '')) ? 'active' : '' }>
                      <Link to={'/'+obj.type + (obj.id != null ? ('/' + obj.id) : '')} title={obj.name}>
                        <em className='icon-grid'></em>
                        <span data-localize='sidebar.nav.SINGLEVIEW'>{obj.name}</span>
                      </Link>
                    </li>
                  }
                }
              ) : null}
            </ul>
            { /* END sidebar nav */ }
          </nav>
        </div>
        { /* END Sidebar (left) */ }
      </aside>
    );
  }

}

Sidebar.contextTypes = {
  router: React.PropTypes.object
};

function mapStateToProps(state, ownProps) {
  return {
    navData: state.rootReducer.nav.navData,
    ownProps: ownProps,
    token: state.rootReducer.user.token
  }
}

function mapDispatchToProps(dispatch) {
  return {
    navAction: bindActionCreators(navAction, dispatch)
  }
}

export default connect(mapStateToProps, mapDispatchToProps)(Sidebar)



