import {ITableObjectsView} from "./protocol/ITableObjectsView";
declare function require(moduleName: string): any;
import * as React from "react";
import PaginationBasic from './PaginationBasic';
import {Grid} from 'react-bootstrap';
import {Row} from 'react-bootstrap';
import {connect} from 'react-redux';
import {bindActionCreators} from 'redux';
import {browserHistory} from 'react-router';
import MyContextMenu from './menu';
import {IColumnView} from "./protocol/IColumnView";
import {BOOLEAN_CHECKBOX} from "../../constants/Attr";
import BoolFormatter from "./formatter/boolFormatter";
let objectListAction = require("./../../actions/ObjectListAction");
let ReactDataGrid = require("react-data-grid")
let Toolbar = require("./ToolbarMy").default;
let Preview = require("./../preview").default;

/**
 *
 */
export interface ObjListProps {
  params: {
    page: string,
    contentCode: string
  },
  sortColumn: string,
  sortDirection: string,
  limit: number,
  offset: number,
  objectListAction: {
    loadObjectList: Function
  },
  token: string,
  objListData: ITableObjectsView
}

export interface ObjListState {
  filters: any,
}

/**
 *
 *
 */
export interface TableRow {
  id: string,
  [name: string]: string;
}
/**
 *
 */
export class ObjList extends React.Component<ObjListProps, ObjListState> {
  /**
   *
   * @param props
   */
  constructor(props: ObjListProps) {
    super(props);
  }

  /**
   *
   * @returns {number}
   */
  getLimit() {
    return 15;
  }

  /**
   *
   */
  componentWillMount() {
    var limit = this.getLimit();
    var offset = limit * (parseInt(this.props.params.page) - 1);
    this.props.objectListAction.loadObjectList({
      contentCode: this.props.params.contentCode,
      limit: limit,
      offset: offset,
      sortColumn: this.props.sortColumn,
      sortDirection: this.props.sortDirection
    }, this.props.token, true);
    this.state = {filters: {}}
  }

  /**
   *
   * @param nextProps
   */
  componentWillReceiveProps(nextProps: ObjListProps) {
    var limit = this.getLimit();
    var offset = limit * (parseInt(nextProps.params.page) - 1);
    var contentCode = nextProps.params.contentCode;
    var page = nextProps.params.page;
    if (this.props.params.contentCode !== contentCode || this.props.params.page !== page) {
      this.props.objectListAction.loadObjectList({
        contentCode: contentCode,
        limit: limit,
        offset: offset,
        sortColumn: this.props.sortColumn,
        sortDirection: this.props.sortDirection
      }, this.props.token, true);
      this.setState({filters: {}});
    }
  }

  /**
   *
   * @param i
   * @returns {TableRow}
   */
  rowGetter(i: number): TableRow {
    if (i == -1) {
      return null;
    }
    let data: Array<any> = this.props.objListData.objectsView[i].data;
    let cols = this.props.objListData.columnsView;
    let result: TableRow = {id: this.props.objListData.objectsView[i].id};
    for (let j = 0; j < cols.length; j++) {
      if (data[j] != null && data[j].objects != null) {
        result[cols[j].key] = data[j] != null ? (data[j].title != null ? data[j].title : data[j].objects.map((obj: any) => obj.title).join(", ")) : "";
      } else {
        result[cols[j].key] = data[j] != null ? (data[j].title != null ? data[j].title : data[j] ) : "";
      }
    }
    return result;
  }


  handleFilterChange(filter: any) {
    console.log(filter);
     var newFilters = this.state.filters;
     if (filter.filterTerm) {
     newFilters[filter.column.key] = filter.filterTerm;
     } else {
     delete newFilters[filter.column.key];
     }
     this.setState({...this.state, filters: newFilters});
    this.props.objectListAction.loadObjectList({
      contentCode: this.props.params.contentCode,
      limit: this.props.limit,
      offset: this.props.offset,
      sortColumn: this.props.sortColumn,
      sortDirection: this.props.sortDirection,
      query : newFilters
    }, this.props.token, false);
  }


  onClearFilters() {
    console.log('onClearFilters');
    //all filters removed
    //this.setState({filters: {}});
  }

  /**
   * load objlist with other sort
   * @param sortColumn
   * @param sortDirection
   */
  handleGridSort(sortColumn: string, sortDirection: string) {
    var limit = this.props.limit != null ? this.props.limit : this.getLimit();
    var offset = this.props.offset != null ? this.props.offset : limit * (parseInt(this.props.params.page) - 1);
    this.props.objectListAction.loadObjectList({
      contentCode: this.props.params.contentCode,
      limit: limit,
      offset: offset,
      sortColumn: sortColumn,
      sortDirection: sortDirection,
    }, this.props.token);
  }

  /**
   * load other page in the current objlist with current sort
   * @param page
   */
  loadOtherPage(page: number) {
    var limit = this.getLimit();
    var offset = limit * (page - 1);
    this.props.objectListAction.loadObjectList({
      contentCode: this.props.params.contentCode,
      limit: limit,
      offset: offset,
      sortColumn: this.props.sortColumn,
      sortDirection: this.props.sortDirection
    }, this.props.token);
  }


  deleteRow(e: any, data: any) {
    console.log(data, e);
    //this.state.rows.splice(data.rowIdx, 1);
    //this.setState({...this.state,rows: this.state.rows});
  }

  insertRowAbove(e: any, data: any) {
    console.log(data, e);
    this.insertRow(data.rowIdx);
  }

  insertRowBelow(e: any, data: any) {
    console.log(data, e);
    this.insertRow(data.rowIdx + 1);
  }


  insertRow(rowIdx: any) {
    console.log(rowIdx);
    //var newRow = {
    //  id: 0,
    //  title: 'New at ' + (rowIdx + 1),
    //   count: 0
    // }
    //this.state.rows.splice(rowIdx, 0, newRow);
    // this.setState({rows: this.state.rows});
  }

  /**
   * create new Object component
   */
  createObj() {
    browserHistory.push('/new/' + this.props.params.contentCode + '/null/null');
  }

  /**
   * Open Object component
   * @param data
   */
  rowSelect(rowIdx: number, row: TableRow) {//data: Array<TableRow>
    if (rowIdx == -1) {
      return;
    }
    browserHistory.push('/obj/' + this.props.params.contentCode + '/' + row.id + '/')
  }

  /**
   *
   * Render Table object
   */
  render() {
    if (this.props.objListData != null) {
      //objdata
      let objListData = this.props.objListData;

      let columnsView = objListData.columnsView;
      columnsView = columnsView.map((col: IColumnView) => {
        if (col.typeView.code == BOOLEAN_CHECKBOX) {
          col.formatter = BoolFormatter;
        }
        return col;
      });


      //menu
      // let myContextMenu = <MyContextMenu onRowDelete={(e: any, data: any)=>this.deleteRow(e,data)}
      //                                    onRowInsertAbove={()=>this.insertRowAbove}
      //                                    onRowInsertBelow={(e: any, data: any)=>this.insertRowBelow(e,data)}
      //
      //                                   rowIdx={null} idx={null}/>;

      //toolbar component
      let toolbar = <Toolbar enableFilter={true} onAddRow={()=>this.createObj()} onToggleFilter={null}
                             filterRowsButtonText="Фильтровать" addRowButtonText="Создать"/>;
      return <Grid fluid={true}>
        <Row>
          <ReactDataGrid columns={columnsView}
                         onGridSort={(sortColumn: string, sortDirection: string)=>this.handleGridSort(sortColumn,sortDirection)}
                         rowGetter={(i: number)=>this.rowGetter(i)}
                         rowsCount={objListData.objectsView.length}
                         minHeight={700}
                         rowKey='id'
                         onRowClick={(rowIdx:number, row:TableRow)=>this.rowSelect(rowIdx,row)}
                         toolbar={toolbar}
                         enableRowSelect={true}
                         onAddFilter={(filter: any)=>this.handleFilterChange(filter)}
                         onClearFilters={()=>this.onClearFilters()}
                         contextMenu={null}
          />
        </Row>
        < Row >
          <PaginationBasic page={this.props.params.page}
                           cnt={(objListData.cnt-1)/this.getLimit()+1}
                           loadOtherPage={(page: number)=>this.loadOtherPage(page)}/>
        </Row>
      </Grid>
    } else {
      return <Preview/>
    }
  }
}


function mapStateToProps(state: any) {
  var objListData = state.rootReducer.objectlist.get('objListData');
  return {
    objListData: objListData != null ? objListData.table : null,
    limit: objListData != null ? objListData.limit : null,
    offset: objListData != null ? objListData.offset : null,
    sortColumn: objListData != null ? objListData.sortColumn : null,
    sortDirection: objListData != null ? objListData.sortDirection : null,
    token: state.rootReducer.user.token
  }
}


function mapDispatchToProps(dispatch: any) {
  return {
    objectListAction: bindActionCreators(objectListAction, dispatch)
  }
}

export default connect(mapStateToProps, mapDispatchToProps)(ObjList)



