import {
  SEARCH_LIST_REQUEST,
  SEARCH_LIST_FAIL,
  SEARCH_LIST_SUCCESS
} from '../constants/SearchList';

import ServerIo from './../server/ServerIo';
import {SearchResultView} from "../components/search/protocol/SearchResultView";

export function fullTextSearch(payload: {query:string}, token: string) {

  return (dispatch: any) => {

    //if (resetObjlist) {
      dispatch({
        type: SEARCH_LIST_REQUEST
      })
    //}

    var serverIo = new ServerIo(token);
    serverIo.fullTextSearch({
      query: payload.query
    }).done((data:  Array<SearchResultView>) => {
      dispatch({
        type: SEARCH_LIST_SUCCESS,
        payload: {
          data: data
        }
      })
    }).fail(() => {
      dispatch({
        type: SEARCH_LIST_FAIL,
        payload: {}
      })
    });
  }
}



