import {IFullObjectView} from "./protocol/IFullObjectView";
declare function require(moduleName: string): any;
import * as React from "react";
import {connect} from "react-redux";
import {bindActionCreators} from "redux";
import  {Row} from "react-bootstrap";
import  {Col} from "react-bootstrap";
import  {Panel} from "react-bootstrap";
import  {Grid} from "react-bootstrap";
import Attr from "./attrs";
import {MULTILINK_BROWSE, BACK_REFERENCE_LIST, BACK_REFERENCE_TABLE} from "../../constants/Attr";
var objectAction = require("./../../actions/ObjectAction");

var Preview = require("./../preview").default;
var Report = require("./../utils/report").default;
var UpdateObj = require("./../utils/obj/update").default;
var DeleteObj = require("./../utils/obj/delete").default;
var Claim = require("./../utils/obj/wf/claim").default;
var Complete = require("./../utils/obj/wf/complete").default;
var StartWfUtils = require("./../utils/obj/wf/start").default;
var ContentWrapper = require("../Layout/ContentWrapper").default;

export interface ObjProps {
  handleChange: Function ,
  handleChange2: Function ,
  innerobj: boolean,
  route: {
    name: string
  },
  objectAction: {createObject: Function, loadObject: Function},
  objData: IFullObjectView,
  params: {
    objId: string,
    backLinkAttr: string,
    contentCode: string
    obj: any,
    data: any
  },
  token: string
}


export class ObjBase extends React.Component<ObjProps, undefined> {


  constructor(props: ObjProps) {
    super(props);
  }


  componentWillMount() {
    if (this.props.innerobj == null) {
      if (this.props.route.name == 'newObj') {
        this.props.objectAction.createObject({
          backLinkAttr: this.props.params.backLinkAttr,
          objId: this.props.params.objId,
          contentCode: this.props.params.contentCode
        }, this.props.token);
      } else {
        this.props.objectAction.loadObject({
          id: this.props.params.obj,
          contentCode: this.props.params.contentCode
        }, this.props.token);
      }
    }
  }


  componentWillReceiveProps(nextProps: ObjProps) {
    if (this.props.innerobj == null) {
      if (nextProps.params.data !== this.props.params.data || nextProps.params.obj !== this.props.params.obj) {
        if (nextProps.route.name == 'newObj') {
          this.props.objectAction.createObject({
            backLinkAttr: nextProps.params.backLinkAttr,
            objId: nextProps.params.objId,
            contentCode: nextProps.params.contentCode
          }, this.props.token);
        } else {
          this.props.objectAction.loadObject({
            id: nextProps.params.obj,
            contentCode: nextProps.params.contentCode
          }, this.props.token);
        }
      }
    }
  }

  componentWillUnmount() {
    //this.props.objData = null;
  }


  getData() {
    var data: {[key: string]: string;} = {};
    this.props.objData.fields.map(field => {
        if (field.type.code != BACK_REFERENCE_LIST && field.type.code != BACK_REFERENCE_TABLE) {
          var val = (this.refs['attr_' + field.code] as any).getValue();
          data[field.code] = (val !== 'null' ? val : null);
          console.log(field.code + ' ' + val);
        }
      }
    )
    return {
      objId: this.props.params != null ? this.props.params.obj : null,
      contentCode: this.props.params != null ? this.props.params.contentCode : null,
      attrs: data
    };
  }


  handlerAttr2() {
    if (this.props.handleChange2 != null) {
      this.props.handleChange2(this.getData());
    }
  }


  render() {
    if (this.props.objData != null) {
      var obj = this.props.objData;
      var docum;
      if (this.props.params != null && this.props.params.contentCode == 'myTask') {
        docum = <div>
          <Col xs={6}>
          </Col>
          <Col xs={1}>
            <Claim objIds={obj.id} contentCode={this.props.params.contentCode} token={this.props.token}/>
          </Col>
          <Col xs={1}>
            <Complete objIds={obj.id} contentCode={this.props.params.contentCode} token={this.props.token}
                      text="Утвердить"
                      data={1}/>
          </Col>
          <Col xs={1}>
            <Complete objIds={obj.id} contentCode={this.props.params.contentCode} token={this.props.token}
                      text="Отказать"
                      data={2}/>
          </Col>
        </div>
      } else {
        docum = <Col xs={8}>
          <Col xs={1}>
            <StartWfUtils objIds={obj.id} contentCode={this.props.params.contentCode} token={this.props.token}/>
          </Col>
        </Col>
      }

      //StartWfUtils
      var doc: Array<any> = obj.fields.map(field => {
          return <fieldset>
            <Attr objId={obj.id} field={field} value={obj.values[field.code]}
                  variants={obj.variants[field.code]}
                  viewSettig={obj.viewSettig[field.code]}
                  ref={'attr_' + field.code}
                  token={this.props.token}
                  handleChange={()=>this.handlerAttr2()}/>
          </fieldset>
        }
      );
      if (this.props.innerobj == null) {
        return <ContentWrapper>
          <h3>{'Заголовок: ' + obj.title}
            <small>{' Идентификатор ' + obj.id}</small>
          </h3>
          <Panel header="">
            <fieldset>
              <Grid fluid={true}>
                <Row>
                  {docum}
                  <Col xs={1}>
                    <UpdateObj getObj={()=>this.getData()} token={this.props.token}
                               new={this.props.route.name == 'newObj' ? 'new' : ''}/>
                  </Col>
                  <Col xs={1}>
                    <DeleteObj objId={obj.id} contentCode={this.props.params.contentCode}
                               token={this.props.token}/>
                  </Col>
                  <Col xs={1}>
                    <Report objIds={obj.id}/>
                  </Col>

                </Row>
                <Row>
                </Row>
              </Grid>
            </fieldset>
            {doc}
          </Panel>
        </ContentWrapper>
      } else {
        return <Panel header={obj.title}>{doc}</Panel>;
      }
    } else {
      return <Preview></Preview>
    }
  }
}


function mapStateToProps(state: any) {
  return {
    objData: state.rootReducer.object.get('objData'),
    token: state.rootReducer.user.token
  }
}


function mapDispatchToProps(dispatch: any) {
  return {
    objectAction: bindActionCreators(objectAction, dispatch)
  }
}

export default connect(mapStateToProps, mapDispatchToProps)(ObjBase)



