import {
  OBJECT_NAV_REQUEST,
  OBJECT_NAV_FAIL,
  OBJECT_NAV_SUCCESS
} from '../constants/Nav';

import {
  LOGIN_FAIL
} from './../constants/User'

import ServerIo from './../server/ServerIo';

export function loadNav(payload,token) {

  return (dispatch) => {
    dispatch({
      type: OBJECT_NAV_REQUEST
    })
    var serverIo = new ServerIo(token);
    serverIo.getNav({ws: payload.ws}).done(data=> {
      dispatch({
        type: OBJECT_NAV_SUCCESS,
        payload: {
          data: data
        }
      })

    }).fail((data)=> {
      console.log(data.status);
      if(data.status==401){
        dispatch({
          type: LOGIN_FAIL,
          payload: {}
        })
      }else{
        dispatch({
          type: OBJECT_NAV_FAIL,
          payload: {}
        })
      }
    });
  }
}
