import * as React from "react";
declare function require(moduleName: string): any;
import  {Row} from "react-bootstrap";
import  {Col} from "react-bootstrap";
import  {FormGroup} from "react-bootstrap";


import Numeric from "./numeric_attr";
import Boolean from "./boolean_attr";

import LinkAttr from "./link_attr_typeahead";
import String from "./string_attr";
import Date from "./date_attr";
import File from "./file_attr";
import SelectBox from "./select_box_attr";
import BackLink from "./backlink_attr";
import BackLinkTable from "./backlink_table_attr";
import MultyLinkTable from "./multylink_table_attr";



import InnerObj from "./inner_obj";
let TextBox = require("./text_attr").Text;

import {
  NUMERIC_INTEGER,
  NUMERIC_DECIMAL,
  BOOLEAN_CHECKBOX,
  BOOLEAN_STRING,
  LINK_SELECTBOX,
  LINK_BROWSE,
  LINK_RADIOBUTTON,
  MULTILINK_BROWSE,
  MULTILINK_CHECKBOX,
  STRING_SINGLE,
  STRING_MULTILINE,
  DATE_DATE_ONLY,
  DATE_TIME_ONLY,
  DATE_BOTH,
  BACK_REFERENCE_LIST,
  BACK_REFERENCE_TABLE,
  FILE_SIMPLE,
  LINK_INNER_OBJECT
} from "./../../../constants/Attr";
import FormEvent = React.FormEvent;
import {IFieldView} from "../protocol/IFieldView";
import {IObjectView} from "../protocol/IObjectView";
import {IViewSetting} from "../protocol/IViewSetting";
//import Row from "react-bootstrap/lib/Row";
//import Radio from "react-bootstrap/lib/Radio";
//import Booleans from "./booleans_attr";
//import BackLinkTable from "./backlink_table_attr";
//import {Link} from "react-router";


export interface AttrProps {
  handleChange: Function ,
  field: IFieldView,
  variants: Array<IObjectView>,
  viewSettig: IViewSetting,
  value: any,
  token: string,
  objId: string
}

export interface AttrState {
  field: IFieldView,
  variants: Array<IObjectView>,
  viewSettig: IViewSetting,
  value: any
}


export default class Attr extends React.Component<AttrProps, AttrState> {


  constructor(props: AttrProps) {
    super(props);
    this.state = {field: props.field, value: props.value, variants: props.variants, viewSettig: props.viewSettig};
  }


  getValue() {
    return this.state.value;
  }


  componentWillReceiveProps(nextProps: AttrProps) {
    this.setState({
      field: nextProps.field,
      value: nextProps.value,
      variants: nextProps.variants,
      viewSettig: nextProps.viewSettig
    });
  }

  handleChange(event: any, funk: any) {
    var value;
    if (funk != null) {
      value = funk(event);
    } else {
      value = (event.target as any).value;
    }
    this.setState({
      field: this.state.field,
      variants: this.state.variants,
      viewSettig: this.state.viewSettig,
      value: value
    });
    if (this.props.handleChange != null) {
      this.props.handleChange();
    }
  }


  getAttrByCode(attrCode: string, field: IFieldView, value: any, variants: Array<IObjectView>, title: string) {
    switch (attrCode) {
      case NUMERIC_INTEGER:
        return <Numeric handleChange={(event:FormEvent<any>, funk:any)=>this.handleChange(event,funk)} value={value}/>;
      case NUMERIC_DECIMAL:
        return <Numeric handleChange={(event:FormEvent<any>, funk:any)=>this.handleChange(event,funk)} value={value}/>;
      case BOOLEAN_CHECKBOX:
        return <Boolean handleChange={(event:FormEvent<any>, funk:any)=>this.handleChange(event,funk)} value={value}/>;
      case BOOLEAN_STRING:
        return //<Text onChange={(event:FormEvent<any>, funk:any)=>this.handleChange(event,funk)} title={title}/>;
      case LINK_SELECTBOX:
        return <SelectBox handleChange={(event:FormEvent<any>, funk:any)=>this.handleChange(event,funk)} value={value}
                          title={title} variants={variants}/>
      case LINK_RADIOBUTTON:
        return null;//<Radio onChange={(event:FormEvent<any>, funk:any)=>this.handleChange(event,funk)}>{title}</Radio>;
      case LINK_BROWSE:
        return <LinkAttr handleChange={(event:FormEvent<any>, funk:any)=>this.handleChange(event,funk)} value={value}
                         variants={variants} contentCode={field.linkMetaClass} token={this.props.token}
                         async={true} showOpenLink = {true}/>;
      case MULTILINK_BROWSE:
        return <MultyLinkTable handleChange={(event:FormEvent<any>, funk:any)=>this.handleChange(event,funk)}
                              value={value} objId={this.props.objId} backLinkAttr={field.code} clazz={field.linkMetaClass} token={this.props.token}/>;//value.map(obj=>
      case MULTILINK_CHECKBOX:
        return null;//<Booleans onChange={(event:FormEvent<any>, funk:any)=>this.handleChange(event,funk)} value={value}/>;
      case STRING_SINGLE:
        return <String handleChange={(event:FormEvent<any>, funk:any)=>this.handleChange(event,funk)} value={value}/>;
      case STRING_MULTILINE:
        return <TextBox handleChange={(event:any, funk:any)=>this.handleChange(event,funk)} value={value}/>;
      case DATE_DATE_ONLY:
        return <Date handleChange={(event:FormEvent<any>, funk:any)=>this.handleChange(event,funk)} value={value}/>;
      case DATE_TIME_ONLY:
        return <Date handleChange={(event:FormEvent<any>, funk:any)=>this.handleChange(event,funk)} value={value}/>;
      case DATE_BOTH:
        return <Date handleChange={(event:FormEvent<any>, funk:any)=>this.handleChange(event,funk)} value={value}/>;
      case BACK_REFERENCE_LIST:
        if (value != null) {
          return <BackLink value={value} objId={this.props.objId} clazz={field.linkMetaClass}
                           backLinkAttr={field.backLinkAttr}

                           handleChange={(event:FormEvent<any>, funk:any)=>this.handleChange(event,funk)} />
        } else {
          return <div></div>
        }
      case BACK_REFERENCE_TABLE:
        return <BackLinkTable value={value} objId={this.props.objId}
                              handleChange={(event:FormEvent<any>, funk:any)=>this.handleChange(event,funk)}
                              backLinkAttr={field.code}   />
      case FILE_SIMPLE:
        return <File handleChange={(event:FormEvent<any>, funk:any)=>this.handleChange(event,funk)} value={value}
                     token={this.props.token}/>;
      case LINK_INNER_OBJECT:
        return <InnerObj objData={value} token={this.props.token}
                         handleChange={(event:FormEvent<any>, funk:any)=>this.handleChange(event,funk)}/>;
      default:
        return <String handleChange={(event:FormEvent<any>, funk:any)=>this.handleChange(event,funk)} value={value}/>;
    }
  }


  render() {
    var attr = this.getAttrByCode(this.state.viewSettig.typeView.code, this.state.field, this.state.value, this.state.variants, null);
    return <FormGroup>
      <Col lg={1}>
      </Col>
      <Col lg={10}>
        <Row>
          <label className="control-label">{this.state.field.title}</label>
        </Row>
        <Row>
          {attr}
        </Row>
      </Col>
    </FormGroup>
  }
}




