import {
  OBJECT_REQUEST,
  OBJECT_FAIL,
  OBJECT_SUCCESS,
  OBJECT_DELETE
} from '../constants/Object';
import {fromJS} from 'immutable';
import {IFullObjectView} from "../components/obj/protocol/IFullObjectView";


const initialState = fromJS({objData: null});

export default function object(state = initialState, action: {type: string, payload: {data: IFullObjectView}}) {
  switch (action.type) {
    case OBJECT_REQUEST:
      return state.merge(initialState);
    case OBJECT_SUCCESS:
      return state.update('objData', () => action.payload.data);
    case OBJECT_DELETE:
      return state.merge(initialState);
    case OBJECT_FAIL:
      return state;
    default:
      return state;
  }
}



