import * as React from "react";

declare function require(moduleName: string): any;

var DatePicker = require('react-datepicker');
var moment = require('moment');

require('react-datepicker/dist/react-datepicker.css');


export interface DateProps {
  handleChange: Function ,
  value: Array<Object>
}


export interface DateState {
  date: any
}

export default class Date extends  React.Component<DateProps, DateState> {

  constructor(props:DateProps) {
    super(props);
    moment.lang('ru');
    var startDate = props.value;
    var date = null;
    if (startDate == null) {
      date = moment();
    } else {
      date = moment(startDate);
    }
    this.state = {date: date}
    this.funk(date);
  }

  funk(startDate: any) {
    this.setState({date: startDate});
    return this.props.handleChange(startDate, (startDate: any) => startDate.format('DD.MM.YYYY h:mm:ss'));
  }


  render() {
    return <DatePicker
      selected={this.state.date}
      onChange={(eve: any)=>this.funk(eve)}/>;
  }
}




