import React from 'react';

export default class Preview extends React.Component {


  constructor(props) {
    super(props);
    this.state = {image: false};
    this.mount = false;
  }


  componentDidMount() {
    this.mount = true;
    this.setState({image: false});
    var stateInner = this;
    setTimeout(function () {
      if (stateInner.mount == true) {
        stateInner.setState({image: true});
      }
    }, 600);
  }

  componentWillUnmount() {
    this.mount = false;
  }

  render() {
    if (this.state.image == true) {
      return <div className='container'>
        <div className='row'>
          &nbsp;
        </div>
        <div className='row'>
          &nbsp;
        </div>
        <div className='row'>
          &nbsp;
        </div>
        <div className='row'>
          <div className='col-md-12'>
            <div className='whirl helicopter'>
            </div>
          </div>
        </div>
      </div>
    } else {
      return <div></div>
    }
  }
}





