import {
  OBJECT_UPDATE_REQUEST,
  OBJECT_UPDATE_FAIL,
  OBJECT_FAIL,
  OBJECT_SUCCESS,
  OBJECT_DELETE
} from "../constants/Object";
import ServerIo from "./../server/ServerIo";
import {browserHistory} from "react-router";

export function updateObject(payload, token) {
  return (dispatch) => {
    dispatch({
      type: OBJECT_UPDATE_REQUEST
    })
    var serverIo = new ServerIo(token);
    serverIo.updateObj(payload.obj).done(data=> {

      dispatch({
        type: OBJECT_SUCCESS,
        payload: {
          data: data
        }
      })


    }).fail(()=> {
      dispatch({
        type: OBJECT_UPDATE_FAIL,
        payload: {}
      })
    });
  };
}

export function createObject(payload, token) {
  return (dispatch) => {
    dispatch({
      type: OBJECT_UPDATE_REQUEST
    })
    var serverIo = new ServerIo(token);
    serverIo.createObj(payload.obj).done(data=> {

      console.log(data);
      browserHistory.push('/obj/' + data.contentCode + '/' + data.id + '/');

      //dispatch({
      //  type: OBJECT_SUCCESS,
      //  payload: {
      //    data: data
      //  }
      // })

    }).fail(()=> {
      dispatch({
        type: OBJECT_UPDATE_FAIL,
        payload: {}
      })
    });
  };
}

export function deleteObject(payload, token) {
  return (dispatch) => {
    dispatch({
      type: OBJECT_UPDATE_REQUEST
    })
    var serverIo = new ServerIo(token);
    serverIo.deleteObj(payload).done(data=> {
      browserHistory.push('/objlist/' + payload.contentCode + '/page/1');
      dispatch({
        type: OBJECT_DELETE,
        payload: {
          data: data
        }
      })
    }).fail(()=> {
      dispatch({
        type: OBJECT_FAIL,
        payload: {}
      })
    });
  }
}





