declare function unescape(str: string): any;

var uuid = require("uuid-js");
var $ = require("jquery");

export default class ServerIo {
  private token: string;


  constructor(token: string) {
    this.token = token;
  }


  getObjList(data: {sortColumn: string, sortDirection: string, contentCode: string, offset: string, limit: string, query: any}) {
    var sort = null;
    var query = null;

    if (data.query != null) {
      query = "&query=" + window.btoa(unescape(encodeURIComponent((JSON.stringify(data.query)))));
    }

    if (data.sortColumn != null) {
      sort = "&sortColumn=" + data.sortColumn + "&sortDirection=" + data.sortDirection;
    }


    return this.getData('/rest/pr/queryservices/obj/' + data.contentCode + '?offset=' + data.offset + '&limit=' + data.limit + (sort != null ? sort : '') + (query != null ? query : ''));
  }


  fullTextSearch(param: {query: string}) {
    return this.getData('/rest/pr/fulltext/search?text=' + param.query);
  }


  getObj(data: {contentCode: string, id: string}): any {
    return this.getData('/rest/pr/queryservices/obj/' + data.contentCode + '/' + data.id);
  }

  getEmptyObj(data: {contentCode: string, backLinkAttr: string, objId: string}) {
    return this.getData('/rest/pr/queryservices/newObj/' + data.contentCode + '/' + data.backLinkAttr + '/' + data.objId);
  }

  updateObj(data: {attrs: any, contentCode: string, objId: string}): any {
    return this.putData(data.attrs, '/rest/pr/queryservices/obj/' + data.contentCode + '/' + data.objId);
  }

  createObj(data: {attrs: any, contentCode: string}): any {
    return this.sendData(data.attrs, '/rest/pr/queryservices/obj/' + data.contentCode + '/');
  }


  deleteObj(data: {contentCode: string, objId: string}) {
    return this.deleteData('/rest/pr/queryservices/obj/' + data.contentCode + '/' + data.objId);
  }

  getNav() {
    return this.getData('/rest/pr/queryservices/nav');
  }


  getAuth(data: any) {
    return this.sendDataSync(data, '/rest/pb/login/login');
  }

  sendFile(data: any) {
    return $.ajax({
      type: 'POST',
      url: '/rest/pr/upload/uploadFile',
      data: data,
      cache: false,
      enctype: 'multipart/form-data',
      headers: {
        'myresttoken': this.token
      },
      processData: false,
      contentType: false
    })
  }


  submitCommand(command: string, data: any) {
    var dataCommand = {
      id: uuid.create().hex,
      type: command,
      title: '',
      descriprion: '',
      object: data
    }
    return this.sendData(dataCommand, '/rest/pr/commandservice/submitCommand');
  }

  deleteData(url: string) {
    return $.ajax({
      type: 'DELETE',
      url: url,
      headers: {
        'myresttoken': this.token
      },
      async: 'true',
      dataType: 'json',
      contentType: 'application/json; charset=UTF-8'
    })
  }


  putData(data: any, url: string) {
    return $.ajax({
      type: 'PUT',
      url: url,
      data: data != null ? JSON.stringify(data) : null,
      headers: {
        'myresttoken': this.token
      },
      async: 'true',
      dataType: 'json',
      contentType: 'application/json; charset=UTF-8'
    })
  }


  getData(url: string) {
    return $.ajax({
      type: 'GET',
      url: url,
      headers: {
        'myresttoken': this.token
      },
      async: 'true',
      dataType: 'json',
      contentType: 'application/json; charset=UTF-8'
    })
  }


  sendData(data: any, url: string) {
    return $.ajax({
      type: 'POST',
      url: url,
      data: data != null ? JSON.stringify(data) : null,
      headers: {
        'myresttoken': this.token
      },
      async: 'true',
      dataType: 'json',
      contentType: 'application/json; charset=UTF-8'
    })
  }


  sendDataSync(data: any, url: string) {
    return $.ajax({
      type: 'POST',
      url: url,
      data: data != null ? JSON.stringify(data) : null,
      headers: {
        'myresttoken': this.token
      },
      async: 'false',
      dataType: 'json',
      contentType: 'application/json; charset=UTF-8'
    })
  }
}



