import {
  OBJECT_LIST_REQUEST,
  OBJECT_LIST_FAIL,
  OBJECT_LIST_SUCCESS
} from '../constants/ObjectList';
import ServerIo from './../server/ServerIo';
import {ITableObjectsView} from "../components/objlist/protocol/ITableObjectsView";

export function loadObjectList(payload: {contentCode: string, limit: string, offset: string, sortColumn: string, sortDirection: string, query:any}, token: string, resetObjlist: boolean) {

  return (dispatch: any) => {

    if (resetObjlist) {
      dispatch({
        type: OBJECT_LIST_REQUEST
      })
    }

    var serverIo = new ServerIo(token);
    serverIo.getObjList({
      contentCode: payload.contentCode,
      limit: payload.limit,
      offset: payload.offset,
      sortColumn: payload.sortColumn,
      sortDirection: payload.sortDirection,
      query: payload.query,
    }).done((data: ITableObjectsView) => {
      dispatch({
        type: OBJECT_LIST_SUCCESS,
        payload: {
          data: {
            table: data,
            limit: payload.limit,
            offset: payload.offset,
            sortColumn: payload.sortColumn,
            sortDirection: payload.sortDirection,
            query: payload.query
          }
        }
      })

    }).fail(() => {
      dispatch({
        type: OBJECT_LIST_FAIL,
        payload: {}
      })
    });
  }
}



