//import {browserHistory} from "react-router";
import {
  LOGIN_REQUEST,
  LOGIN_FAIL,
  LOGIN_SUCCESS,
  LOGOUT_SUCCES
} from './../constants/User'
import cookie from 'react-cookie';
import  moment from 'moment';


const initialState = {
  token: cookie.load('myresttoken'),
  name: cookie.load('name'),
  isAuthenticated: cookie.load('isAuthenticated')
}



export default function user(state = initialState, action) {

  switch (action.type) {

    case LOGIN_REQUEST:
      // TODO
      return state;

    case LOGIN_SUCCESS:

      var cur = moment();
      var end = moment().endOf('day');
      var maxAge = end.diff(cur) / 1000;
      cookie.save('myresttoken', action.payload.token, {path: '/', maxAge: maxAge});
      cookie.save('name', action.payload.name, {path: '/', maxAge: maxAge});
      cookie.save('isAuthenticated', action.payload.isAuthenticated, {path: '/', maxAge: maxAge});


      return {
        ...state,
        token: action.payload.token,
        name: action.payload.name,
        isAuthenticated: action.payload.isAuthenticated
      };

    case LOGIN_FAIL:
      cookie.remove('myresttoken');
      cookie.remove('name');
      cookie.remove('isAuthenticated');
      //browserHistory.push('/login');
      return {...state, token: null, name: null, isAuthenticated: false};

    case LOGOUT_SUCCES:
      cookie.remove('myresttoken');
      cookie.remove('name');
      cookie.remove('isAuthenticated');
      //browserHistory.push('/login');
      return {...state, token: null, name: null, isAuthenticated: false};


    default:
      return state;
  }
}



