import * as React from "react";
import  {Checkbox} from "react-bootstrap";


export interface BoolFormatterProps {
  value: boolean
}


export default class BoolFormatter extends React.Component<BoolFormatterProps, undefined> {


  constructor(props: BoolFormatterProps) {
    super(props);
  }

  render() {
    var checked = this.props.value;
    if (checked == true) {
      return <Checkbox  disabled ={true} checked/>;
    } else {
      return <Checkbox disabled ={true}/>;
    }
  }

}



