import {
  OBJECT_LIST_REQUEST,
  OBJECT_LIST_FAIL,
  OBJECT_LIST_SUCCESS
} from '../constants/ObjectList';
import {fromJS} from 'immutable';
import {ITableObjectsView} from "../components/objlist/protocol/ITableObjectsView";

const initialState = fromJS({objListData: null});

export default function object(state = initialState, action:{type:string,payload:{data:ITableObjectsView}}) {
  switch (action.type) {
    case OBJECT_LIST_REQUEST:
      return  state.merge(initialState);
    case OBJECT_LIST_SUCCESS:
      return state.update('objListData', () => action.payload.data);
    case OBJECT_LIST_FAIL:
      return state;
    default:
      return state;
  }
}






