import React from 'react';
import {Toolbar} from 'react-data-grid-addons'
import Grid from 'react-bootstrap/lib/Grid';
import Col from 'react-bootstrap/lib/Col';
import Button from 'react-bootstrap/lib/Button';

import Row from 'react-bootstrap/lib/Row';

export default class ToolbarMy extends Toolbar {

  constructor(props) {
    super(props);
  }


  render() {
    return <Grid fluid={true}>
      <Row>
        <Col lg={11}>
          {this.props.onAddRow ? <Button bsStyle='primary' bsSize='small'
                                         onClick={this.onAddRow}> {this.props.addRowButtonText }</Button> : null}
        </Col>
        <Col lg={1}>
          { this.props.enableFilter ?
            <Button bsStyle='primary' bsSize='small'
                    onClick={this.props.onToggleFilter}> {this.props.filterRowsButtonText}</Button>
            : null}
        </Col>
      </Row>
    </Grid>
  }
}










