import {LOGIN_REQUEST, LOGIN_FAIL, LOGIN_SUCCESS, LOGOUT_SUCCES} from "../constants/User";
import ServerIo from "./../server/ServerIo";
import {browserHistory} from "react-router";

export function login(payload:{login:string,password:string}, token:string) {
  return (dispatch: any) => {
    dispatch({
      type: LOGIN_REQUEST
    })


    var serverIo = new ServerIo(token);
    serverIo.getAuth(
      {
        login: payload.login,
        password: payload.password
      }
    ).done((data: any)=> {
      dispatch({
        type: LOGIN_SUCCESS,
        payload: {
          name: data.login,
          token: data.token,
          isAuthenticated: true
        }
      })
      browserHistory.push('/');
    }).fail(()=> {
      dispatch({
        type: LOGIN_FAIL,
        payload: {}
      })
      browserHistory.push('/login')
    });
  }
}


export function logout() {
  return (dispatch: any) => {
    dispatch({
      type: LOGOUT_SUCCES,
      payload: {
        isAuthenticated: false,
        token: null,
        name: null
      }
    })
  }
}



