import * as React from "react";
import  {FormControl} from "react-bootstrap";
import  {FormGroup} from "react-bootstrap";
import  {ControlLabel} from "react-bootstrap";
import FormEvent = React.FormEvent;
import {IObjectView} from "../protocol/IObjectView";
import {Button} from 'react-bootstrap';
import {browserHistory} from 'react-router';

export interface SelectBoxProps {
  handleChange: Function ,
  title: string,
  value: string,
  variants: Array<IObjectView>
}

export interface SelectBoxState {
  curObject: IObjectView
}


export default class SelectBox extends React.Component<SelectBoxProps, SelectBoxState> {

  constructor(props: SelectBoxProps) {
    super(props);
    var curObject = this.getObjectByValue(props.value);
    this.state = {curObject: curObject}
  }

  getObjectByValue(value: string): IObjectView {
    for (var i = 0; i < this.props.variants.length; i++) {
      var obj = this.props.variants[i];
      if (obj.id == this.props.value) {
        return obj;
      }
    }
  }


  funk(event: FormEvent<any>) {
    var data = (event.target as any).value;

    var curObject = this.getObjectByValue(data);
    this.setState({curObject: curObject});

    if (data === '---') {
      data = null;
    }
    return this.props.handleChange(data, (data: any) => data);
  }


  openObject() {
    browserHistory.push('/obj/' + this.state.curObject.contentCode + '/' + this.state.curObject.id)
  }


  render() {
    var link;
    if (this.state.curObject != null) {
      link = <Button bsStyle='link' bsSize='small' onClick={()=>this.openObject()}>Открыть</Button>
    }
    return <FormGroup controlId='formControlsSelect'>
      <ControlLabel>{this.props.title}</ControlLabel>
      <FormControl ref="selectBox" componentClass='select' placeholder='select'
                   value={this.state.curObject!=null?this.state.curObject.id:null}
                   onChange={(event: FormEvent<any>)=>this.funk(event)}>
        {this.props.variants.map((obj, index) =>
          <option key={index} value={obj.id}>{obj.title}</option>
        )}
      </FormControl>
      {link}
    </FormGroup>
  }
}





