declare function require(moduleName: string): any;
import {OBJECT_REQUEST, OBJECT_FAIL, OBJECT_SUCCESS} from "../constants/Object";
import  ServerIo  from "./../server/ServerIo";



export function loadObject(payload: {id: string, contentCode: string}, token: string) {
  return (dispatch: any) => {
    dispatch({
      type: OBJECT_REQUEST
    })
    var serverIo = new ServerIo(token);
    var loadFun = serverIo.getObj({id: payload.id, contentCode: payload.contentCode});
    loadFun.done((data:any) => {
      dispatch({
        type: OBJECT_SUCCESS,
        payload: {
          data: data
        }
      })
    }).fail(() => {
      dispatch({
        type: OBJECT_FAIL,
        payload: {}
      })
    });
  }
}


export function createObject(payload: {backLinkAttr: string, contentCode: string, objId: string}, token: string) {
  return (dispatch: any) => {
    dispatch({
      type: OBJECT_REQUEST
    })
    var serverIo = new ServerIo(token);
    var loadFun = serverIo.getEmptyObj({
      contentCode: payload.contentCode,
      backLinkAttr: payload.backLinkAttr,
      objId: payload.objId
    });
    loadFun.done((data: any) => {
      dispatch({
        type: OBJECT_SUCCESS,
        payload: {
          data: data
        }
      })
    }).fail(() => {
      dispatch({
        type: OBJECT_FAIL,
        payload: {}
      })
    });
  }
}



