import * as React from "react";
import {Link} from "react-router";
import {browserHistory} from 'react-router';
import  {Row} from "react-bootstrap";
import  {Grid} from "react-bootstrap";
import  {Button} from "react-bootstrap";
import {IObjectView} from "../protocol/IObjectView";


export interface BackLinkProps {
  handleChange: Function ,
  clazz: string,
  backLinkAttr: string,
  objId: string,
  value: {
    objects: Array<IObjectView>
  }
}

export default class BackLink extends React.Component<BackLinkProps, undefined> {

  constructor(props: BackLinkProps) {
    super(props);
  }


  createObj() {
    browserHistory.push('/new/' + this.props.clazz + '/' + this.props.backLinkAttr + '/' + this.props.objId);
  }

  render() {
    var value = this.props.value;
    return <Grid>
      {value.objects.map((obj) =>
        <Row><Link to={'/obj/'+obj.contentCode+'/'+ obj.id}>{obj.title}</Link></Row>
      )}
      <Row>
        <Button bsStyle='primary' bsSize='small'
                onClick={()=>this.createObj()}> Создать</Button>
      </Row>
    </Grid>
  }
}



