import * as React from "react";
import  {Checkbox} from "react-bootstrap";
import FormEventHandler = React.FormEventHandler;
import FormEvent = React.FormEvent;


export interface BooleanProps {
  handleChange: Function ,
  value: boolean
}

export interface Object {
  title: string
}

export default class Boolean extends React.Component<BooleanProps, undefined> {

  constructor(props: BooleanProps) {
    super(props);
  }

  funk(event:FormEvent<Checkbox>) {
    return this.props.handleChange(event, (event:FormEvent<Checkbox>)=> (event.target as any).checked);
  }


  render() {
    var checked = this.props.value;
    if (checked == true) {
      return <Checkbox onChange={event => this.funk(event)} checked/>;
    } else {
      return <Checkbox onChange={event => this.funk(event)}/>;
    }
  }
}






