declare function require(moduleName: string): any;
import * as React from "react";
import {connect} from "react-redux";
import {bindActionCreators} from "redux";

import  {Panel} from "react-bootstrap";

import {SearchResultView} from "./protocol/searchresultview";
var searchAction = require("./../../actions/SearchAction");
var Preview = require("./../preview").default;
var ContentWrapper = require("../Layout/ContentWrapper").default;
import {Link} from "react-router";

export interface SearchProps {
  searchAction: {
    fullTextSearch: Function
  },
  params: {
    query: string
  },
  token: string,
  searchResultList: Array<SearchResultView>
}


export class SearchResult extends React.Component<SearchProps, undefined> {


  constructor(props: SearchProps) {
    super(props);
  }


  componentWillMount() {
    this.props.searchAction.fullTextSearch({
      query: this.props.params.query
    }, this.props.token);
  }


  componentWillReceiveProps(nextProps: SearchProps) {
    if (this.props.params.query !== nextProps.params.query) {
      this.props.searchAction.fullTextSearch({
        query: this.props.params.query
      }, this.props.token);
    }
  }


  render() {
    if (this.props.searchResultList != null) {
      let searchResult = this.props.searchResultList;
      let items = searchResult.map((item: SearchResultView) => {
        return <Panel header={item.title}>
          <Link to={'/obj/'+item.contentCode+'/'+ item.id}>{item.title}</Link>
        </Panel>
      });
      return <ContentWrapper><h3>{'Результаты поиска '}
      </h3>{items}</ContentWrapper>
    } else {
      return <Preview></Preview>
    }
  }
}


function mapStateToProps(state: any) {
  return {
    searchResultList: state.rootReducer.search.get('searchResultList'),
    token: state.rootReducer.user.token
  }
}


function mapDispatchToProps(dispatch: any) {
  return {
    searchAction: bindActionCreators(searchAction, dispatch)
  }
}

export default connect(mapStateToProps, mapDispatchToProps)(SearchResult)



