import React, {Component} from 'react';
import Button from 'react-bootstrap/lib/Button';


export default class ReportUtils extends Component {

  constructor(props) {
    super(props);
  }

  handleClick() {
    var link = document.createElement('a');
    link.download = 'report';
    link.href = '/rest/reportservice/report';
    console.log('/rest/reportservice/report');
    link.click();
  }

  


  render() {
    return (<div>
        <div >
          <Button bsStyle='primary' bsSize='small' onClick={this.handleClick}>Создать отчет</Button>
        </div>
      </div>
    )
  }
}



