import {
  SEARCH_LIST_REQUEST,
  SEARCH_LIST_FAIL,
  SEARCH_LIST_SUCCESS
} from '../constants/SearchList';

import {fromJS} from 'immutable';
import {SearchResultView} from "../components/search/protocol/SearchResultView";

const initialState = fromJS({searchResultList: null});

export default function object(state = initialState, action: {type: string, payload: {data: Array<SearchResultView>}}) {
  switch (action.type) {
    case SEARCH_LIST_REQUEST:
      return state.merge(initialState);
    case SEARCH_LIST_SUCCESS:
      return state.update('searchResultList', () => action.payload.data);
    case SEARCH_LIST_FAIL:
      return state;
    default:
      return state;
  }
}






