import * as React from "react";
import {IObjectView} from "../protocol/IObjectView";

var {asyncContainer, Typeahead} = require('react-bootstrap-typeahead');
const AsyncTypeahead = asyncContainer(Typeahead);


import * as request from 'superagent';
import {browserHistory} from 'react-router';
import {Button} from 'react-bootstrap';
require('react-bootstrap-typeahead/css/Token.css');
require('react-bootstrap-typeahead/css/Typeahead.css');


export interface LinkProps {
  handleChange: Function ,
  value: IObjectView,
  variants: Array<IObjectView>,
  async: boolean,
  token: string,
  contentCode: string
  showOpenLink: boolean
}


export interface TypeaheadObject {
  id: string,
  label: string
}


export interface LinkState {
  value: IObjectView,
  variants: Array<TypeaheadObject>
}

export default class Link_attr_typeahead extends React.Component<LinkProps, LinkState> {

  constructor(props: LinkProps) {
    super(props);
    this.state = {
      variants: !this.props.async ? this.props.variants.map((obj: IObjectView) => {
          return {id: obj.id, label: obj.title}
        }) : [],
      value: props.value
    };

  }

  componentWillMount() {
    if (this.props.value != null) {
      return this.props.handleChange(this.props.value, (value: IObjectView) => value.id);
    }
  }


  funk(selecteds: Array<TypeaheadObject>) {
    var selected = selecteds[0];
    if (selected != null) {
      this.setState({
        value: {id: selected.id, title: selected.label, contentCode: this.props.contentCode},
        variants: this.state.variants
      });
      return this.props.handleChange(selected, (selected: TypeaheadObject) => selected.id);
    } else {
      this.setState({
        value: null,
        variants: this.state.variants
      });
      return this.props.handleChange(selected, (selected: TypeaheadObject): any => null);
    }
  }


  find(query: string) {
    var req = request.get('/rest/pr/queryservices/quickinput/' + this.props.contentCode + '?value=' + query).set('myresttoken', this.props.token).end((err: any, res: request.Response) => {
      if (res.ok) {
        this.setState({
          value: this.state.value, variants: res.body.objects.map((obj: IObjectView) => {
            return {id: obj.id, label: obj.title}
          })
        });
      }
    });
  }

  openObject() {
    browserHistory.push('/obj/' + this.state.value.contentCode + '/' + this.state.value.id)
  }


  render() {
    let sel = Array<TypeaheadObject>();
    if (this.state.value != null) {
      sel = [{id: this.state.value.id, label: this.state.value.title}];
    }


    if (this.state.value != null && this.props.showOpenLink) {
      var link = <Button bsStyle='link' bsSize='small' onClick={()=>this.openObject()}>Открыть</Button>
    }
    if (!this.props.async) {
      return   <Typeahead onChange={(selected:any) => this.funk(selected)}
                          options={this.state.variants }
                          selected={[{id: this.state.value.id, label: this.state.value.title}]}/>
    } else {
      return     <div><AsyncTypeahead selected={sel} onChange={(selected:any) => this.funk(selected)}
                                      onSearch={(query:string) => this.find(query) } options={this.state.variants}/>
        {link}
      </div>

    }
  }
}







