import * as React from "react";
import {IFullObjectView} from "../protocol/IFullObjectView";
import {ObjBase} from "../index";


export interface InnerObjProps {
  handleChange: Function,
  objData: IFullObjectView,
  token: string
}

export default class InnerObj extends React.Component<InnerObjProps, undefined> {

  constructor(props: InnerObjProps) {
    super(props);
  }


  funk(obj: any) {
    console.log(obj);
    // return this.props.handleChange(obj, (obj)=>obj);
    // todo для сохранения вложенного объекта,надо на основании данных из obj,менять данные objData
    // или разделять мету объекта и данные
    return this.props.objData;
  }

  render() {
    return <ObjBase objData={this.props.objData} token={this.props.token} innerobj={true}
                    handleChange2={(obj:any) =>this.funk(obj)} params={null} route={null} handleChange={null}
                    objectAction={null}/>
  }
}



