import * as React from "react";
import  {Button} from "react-bootstrap";

import * as request from 'superagent';

declare function require(moduleName: string): any;
import * as Dropzone from 'react-dropzone';

export interface FileProps {
  handleChange: Function ,
  value: {
    id: string,
    title:string
  },
  token: string
}


export default class File extends React.Component<FileProps, undefined> {

  constructor(props: FileProps) {
    super(props);
  }


  handleClick() {
    var fileId = this.props.value.id;
    var link = document.createElement('a');
    link.download = 'report';
    link.href = '/rest/pr/download/' + fileId;
    link.click();
  }


  onDrop(files: Array<any>) {
    console.log('Received files: ', files);
    var inner_props = this.props;


    var req = request.post('/rest/pr/upload/uploadFile').set('myresttoken', this.props.token);
    files.forEach((file) => {
      req.attach(file.name, file);
    });
    req.end((err:any, res:any) => {
      console.log(res);
      inner_props.handleChange(res, (res:any) => res.text)
    });
  }


  render() {
    if (this.props.value != null) {
      return <div>
          <Button bsStyle='primary' bsSize='small' onClick={()=>this.handleClick()}>{this.props.value.title}</Button>
          <Dropzone onDrop={(files: Array<any>)=>this.onDrop(files)}>
          <div>Try dropping some files here, or click to select files to upload.</div>
          </Dropzone>
        </div>

    } else {
      return (<div>
          <Dropzone onDrop={(files: Array<any>)=>this.onDrop(files)}>
          <div>Try dropping some files here, or click to select files to upload.</div>
          </Dropzone>
        </div>
      )
    }
  }
}




