import * as React from "react";
import {Link} from "react-router";
import  {Panel} from "react-bootstrap";
import  {Table} from "react-bootstrap";
import  {Button} from "react-bootstrap";
import Link_Attr_Typeahead from "./link_attr_typeahead";
import {browserHistory} from 'react-router';
import {TypeaheadObject} from "./link_attr_typeahead";
import ServerIo from "../../../server/ServerIo";
import {ITableObjectsView} from "../../objlist/protocol/ITableObjectsView";
import {IObjectRowView} from "../../objlist/protocol/IObjectRowView";

export interface BackLinkTableProps {
  handleChange: Function ,
  clazz: string,
  objId: string,
  backLinkAttr: string,
  value: ITableObjectsView,
  token: string,
}


export interface BackLinkTableState {
  newObjectId: string
  objects: ITableObjectsView
}


export default class BackLinkTable extends React.Component<BackLinkTableProps, BackLinkTableState> {

  private serverIo: ServerIo;

  constructor(props: BackLinkTableProps) {
    super(props);
    this.state = {objects: props.value, newObjectId: null}
    this.serverIo = new ServerIo(this.props.token);
  }

  createObj() {
    browserHistory.push('/new/' + this.props.clazz + '/' + this.props.backLinkAttr + '/' + this.props.objId);
  }


  typehead(selected: TypeaheadObject, fuck: Function) {
    if (selected != null) {
      //Добавление объекта в состояние текущего объекта для вставки
      this.setState({newObjectId: selected.id, objects: this.state.objects});
    }
  }


  shouldComponentUpdate(nextProps: BackLinkTableProps, nextState: BackLinkTableState) {
    if (nextState.newObjectId != null) {
      return false;
    } else {
      return true;
    }
  }

  addRowToObject() {
    let objects = this.state.objects;
    let newObjectId: string = this.state.newObjectId;
    if (newObjectId != null) {
      this.serverIo.getObjList({
        sortColumn: null,
        sortDirection: null,
        contentCode: this.props.clazz,
        offset: '0',
        limit: '1',
        query: {id: newObjectId}
      }).done((singleObject: ITableObjectsView) => {
        objects.cnt = objects.cnt + 1;
        let singleObjectRowView: IObjectRowView = singleObject.objectsView[0];
        objects.objectsView.push(singleObjectRowView);
        this.props.handleChange(objects, (objects: ITableObjectsView) => objects)
        this.setState({newObjectId: null, objects: objects});
      });
    }
  }



  render() {
    //<Button bsStyle='primary' bsSize='small'>Удалить</Button>
    var value = this.state.objects;
    if (value == null) {
      return <div></div>
    }
    return <Panel header="">
      <fieldset>
        <Link_Attr_Typeahead async={true} contentCode={this.props.clazz} token={this.props.token}
                             value={null} variants={null}
                             handleChange={(selected:TypeaheadObject,fuck:Function)=>this.typehead(selected,fuck)}
                             showOpenLink={false}/>

        <Button bsStyle='default' bsSize='small' onClick={()=>this.addRowToObject()}>Добавить</Button>
      </fieldset>
      <fieldset>
        <Table striped bordered condensed hover>
          <thead>
          <tr>
            {value.columnsView != null ? value.columnsView.map((obj, index) =>
                <th key={index}>
                  {obj.name}
                </th>
              ) : null}
          </tr>
          </thead>
          <tbody>
          {value.objectsView != null ? value.objectsView.map((obj, index) =>
              <tr key={index}>
                {obj.data.map((row, index2) =>
                  <td key={index2}>
                    {typeof row == 'string' ? row : ""}
                  </td>
                )}
                <tr >

                  <Link to={'/obj/'+value.clazz+'/'+ obj.id}>Открыть</Link>
                </tr>
              </tr>
            ) : null}
          </tbody>
        </Table>
      </fieldset>
      <fieldset>
        <Link
          to={'/new/'+this.props.clazz+'/'+this.props.backLinkAttr+'/'+this.props.objId }>Создать</Link>
      </fieldset>

    </Panel>
  }
}





