declare function require(moduleName: string): any;
import * as React from "react";
import {Pagination} from 'react-bootstrap';

export interface PaginationBasicProps {
  loadOtherPage: Function,
  cnt: number,
  page: string

}

export interface PaginationBasicState {
  activePage: number
}

export default class PaginationBasic extends React.Component<PaginationBasicProps, PaginationBasicState> {

  constructor(props: PaginationBasicProps) {
    super(props);
    this.state = {activePage: parseInt(props.page)};
  }


  handleSelect(eventKey: any) {
    this.setState({activePage: eventKey});
    this.props.loadOtherPage(eventKey);
  }

  render() {
    return (
      <Pagination
        bsSize='medium'
        items={this.props.cnt}
        activePage={this.state.activePage}
        onSelect={(eventKey: any)=>this.handleSelect(eventKey)}/>
    );
  }
}






