import * as React from "react";
import  {FormControl} from "react-bootstrap";

export interface StringProps {
  handleChange: any ,
  value: string
}

export default class String extends React.Component<StringProps, undefined>  {

  constructor(props:StringProps) {
    super(props);
  }

  render() {
    return <FormControl onChange={this.props.handleChange} value={this.props.value}/>
  }
}



